import {
  Fancybox,
   Panzoom
} from "@fancyapps/ui";

Fancybox.bind('[data-fancybox]', {
  dragToClose: false,
  closeButton: "top",

  Image: {
    zoom: true,
    fit: "cover"
  },

  on: {
    ready: (fancybox) => {
      updateBg(fancybox);
    },

    "Carousel.change": (fancybox, carousel, to, from) => {
      updateBg(fancybox, carousel, to);
    },
  },
});

function updateBg(fancybox, carousel = fancybox.Carousel, index = carousel ?.page) {
  if (!carousel || !carousel.slides || !carousel.slides[index]) return;

  const slide = carousel.slides[index];
  const thumbSrc = slide ?.$thumb ?.src || slide ?.thumbSrc || slide ?.src;

  // Захист від помилки: перевіряємо, чи контейнер існує
  if (!fancybox.$container || !thumbSrc) return;

  fancybox.$container.style.setProperty("--bg-image", `url("${thumbSrc}")`);
}
