
export function initScrollToTop(options = {}) {
  const config = {
    scrollThreshold: options.scrollThreshold || 300,
    buttonSelector: options.buttonSelector || '.scroll-to-top',
    ...options
  };

  function handleScroll() {
    const scrollTopBtn = document.querySelector('.scroll-to-top');
    if (!scrollTopBtn) return;

    const scrollPosition = document.documentElement.scrollTop || document.body.scrollTop;

    if (scrollPosition > config.scrollThreshold) {
      scrollTopBtn.classList.add('visible');
    } else {
      scrollTopBtn.classList.remove('visible');
    }
  }

  function handleScrollTopClick(e) {
    e.preventDefault();
    window.scrollTo({
      top: 0
    });
  }

  // Ініціалізація компонента
  const scrollTopBtn = document.querySelector('.scroll-to-top');
  if (scrollTopBtn) {
    scrollTopBtn.addEventListener('click', handleScrollTopClick);
  }

  // Встановлюємо початковий стан
  handleScroll();

  // Додаємо слухач події прокрутки
  window.addEventListener('scroll', handleScroll);

  // Повертаємо метод для очищення, що може бути корисним
  return function cleanup() {
    window.removeEventListener('scroll', handleScroll);
    const scrollTopBtn = document.querySelector(config.buttonSelector);
    if (scrollTopBtn) {
      scrollTopBtn.removeEventListener('click', handleScrollTopClick);
    }
  };
}

// Автоматична ініціалізація кнопки після імпорту модуля
const cleanup = initScrollToTop();

// Експортуємо функцію ініціалізації для можливості налаштувань ззовні
export default initScrollToTop;