import Swiper, {
  Navigation,
  Pagination,
  Autoplay,
  Parallax,
  Scrollbar
} from 'swiper';

// Спільні опції Swiper
const commonSwiperOptions = {
  modules: [Navigation, Pagination, Autoplay, Parallax, Scrollbar],
  slidesPerView: 1,
  mousewheelControl: true,
  keyboardControl: true,
  grabCursor: true,
  loop: true,
  autoplay: {
    delay: 6500,
    disableOnInteraction: false,
  },
};

// // Адаптивність
// const commonSwiperBreakpoints = {
//   breakpoints: {
//     420: {
//       slidesPerView: 1,
//     },
//     450: {
//       slidesPerView: 2,
//       spaceBetween: 20,
//     },
//     800: {
//       slidesPerView: 3,
//       spaceBetween: 30,
//     },
//   },
// };

// Швидкість для слайдера hero
const heroSliderSpeed = 1500;

// Встановлення CSS-змінної через JavaScript
const bodyStyles = window.getComputedStyle(document.body);
const fooBar = bodyStyles.getPropertyValue('--hero-slider-speed');
document.body.style.setProperty('--hero-slider-speed', heroSliderSpeed + 'ms');

// Ініціалізація слайдера
const heroSlider = new Swiper(".banners__slider", {
  ...commonSwiperOptions,
 

  speed: heroSliderSpeed,
  parallax: true,

  autoplay: {
    ...commonSwiperOptions.autoplay,
    delay: 3500,
  },

  navigation: {
    nextEl: ".banners-slider__next",
    prevEl: ".banners-slider__prev",
  },

  pagination: {
    el: '.banners__pagination',
    clickable: true,
    type: 'bullets',
    renderBullet: function (index, className) {
      return `
        <span class="${className} swiper-pagination-bullet--animation">
          <svg width="26" height="26" viewBox="0 0 28 28">
            <circle class="svg__circle" cx="14" cy="14" r="12" fill="none" stroke-width="2"></circle>
            <circle class="svg__circle-inner" cx="14" cy="14" r="6" stroke-width="3"></circle>
          </svg>
        </span>`;
    },
  },
});

document.querySelectorAll('.banners__content').forEach(content => {
  content.addEventListener('mouseenter', () => {
    heroSlider.autoplay.stop();
  });

  content.addEventListener('mouseleave', () => {
    heroSlider.autoplay.start();
  });
});
